
import { defineComponent } from "vue";

export default defineComponent({
  name: "ConfirmationModal",
  emits: ["closeModal", "yesClick"],
  props: ["message"],
  setup(props, { emit }) {
    function onNoClick() {
      emit("closeModal");
    }
    function onYesClick() {
      emit("yesClick");
    }
    return {
      onNoClick,
      onYesClick,
      props
    };
  },
});
